<template>
  <div v-if="recipe && similar">
    <main>
      <div class="container">
        <div class="article-Head d-flex flex-wrap justify-content-between">
          <div class="article-title">
            <SectionTital
              :title="true"
              class="recipe-tital"
              :subtitle="false"
              :type="'main-tital'"
              :titalText="`${recipe.title}`"
            ></SectionTital>
            <ul class="d-flex flex-wrap recipe-info mb-3">
              <li>
                <p>{{ recipe.sourceName }}</p>
              </li>
              <hr class="recipe-info-sp ms-4 me-5" />

              <li class="text-center d-flex align-items-center">
                <i class="fa-solid fa-clock me-3 primary-color"></i>
                <div>
                  <p class="primary-color">PREP TIME</p>
                  <p class="sub-text">
                    {{ recipe.preparationMinutes }} Minutes
                  </p>
                </div>
              </li>
              <hr class="recipe-info-sp mx-4" />
              <li class="text-center d-flex align-items-center">
                <i class="fa-solid fa-clock me-3 primary-color"></i>
                <div>
                  <p class="primary-color">COOK TIME</p>
                  <p class="sub-text">{{ recipe.readyInMinutes }} Minutes</p>
                </div>
              </li>
              <hr class="recipe-info-sp mx-4" />
              <li class="text-center d-flex align-items-center">
                <i class="fa-solid fa-utensils me-3 primary-color"></i>
                <p class="primary-color">{{ recipe.dishTypes[0] }}</p>
              </li>
            </ul>
          </div>
          <div class="button-per d-flex mx-2 align-items-center">
            <div class="text-center">
              <TheBotton
                @click="printPage"
                :type="'secondary_circle'"
                class="p-3 d-flex align-items-center justify-content-center"
              >
                <template #Text>
                  <i class="fa-solid fa-print"></i>
                </template>
              </TheBotton>
              <p class="m-0 me-1 primary-color">PRINT</p>
            </div>
            <div class="text-center mx-2">
              <TheBotton
                :type="'secondary_circle'"
                class="p-3 d-flex align-items-center justify-content-center"
              >
                <template #Text>
                  <i class="fa-solid fa-arrow-up-from-bracket"></i>
                </template>
              </TheBotton>
              <p class="m-0 me-1 primary-color">SHARE</p>
            </div>
          </div>
          <div class="info flex-wrap w-100 d-flex justify-content-between">
            <div class="vid d-flex align-items-center">
              <video
                class="w-100 d-block"
                controls
                :poster="recipe.image"
                src="../assets/The Emperor of Mankind once said.mp4"
              ></video>
            </div>
            <div class="nutrition-info back-primary">
              <h3 class="primary-color nut-title">Nutrition Information</h3>
              <ul>
                <li
                  v-for="nut in recipe.nutrition.nutrients"
                  :key="nut"
                  class="d-flex justify-content-between p-3"
                >
                  <p class="primary-color">{{ nut.name }}</p>
                  <p class="primary-color">{{ nut.amount }}{{ nut.unit }}</p>
                </li>
              </ul>
            </div>
            <div class="w-100 mt-5 pt-4">
              <p class="primary-color" v-html="recipe.summary"></p>
            </div>
          </div>
        </div>
        <div class="article-body mt-5 d-flex flex-wrap justify-content-between">
          <div class="Ingredients instructions">
            <SectionTital
              :title="true"
              class="recipe-Ingredients-tital mb-4"
              :subtitle="false"
              :type="'small-tital'"
              :titalText="'Ingredients'"
            ></SectionTital>
            <ul>
              <li
                class="d-flex pt-3 pb-3"
                v-for="ingredient in recipe.extendedIngredients"
                :key="ingredient.id"
              >
                <input
                  type="checkbox"
                  class=""
                  :id="ingredient.id"
                  :value="ingredient.name"
                />
                <span
                  class="check d-flex align-items-center justify-content-center"
                  ><i class="fa-solid fa-check"></i
                ></span>
                <label class="ms-3 primary-color" :for="ingredient.id">{{
                  ingredient.name
                }}</label>
              </li>
            </ul>
            <SectionTital
              :title="true"
              class="recipe-Ingredients-tital mt-5 mb-4"
              :subtitle="false"
              :type="'small-tital'"
              :titalText="'Instructions'"
            ></SectionTital>
            <ul>
              <li
                class="d-flex pt-3 pb-3"
                v-for="step in recipe.analyzedInstructions[0].steps"
                :key="step.number"
              >
                <input
                  type="checkbox"
                  class=""
                  :id="step.number"
                  :value="step.step"
                />
                <span
                  class="check d-flex align-items-center justify-content-center"
                  ><i class="fa-solid fa-check"></i
                ></span>
                <label class="ms-3 primary-color" :for="step.number">{{
                  step.step
                }}</label>
              </li>
            </ul>
          </div>
          <aside>
            <SectionTital
              :title="true"
              class="aside-tital mb-4"
              :subtitle="false"
              :type="'small-tital'"
              :titalText="'Other Recipe'"
            ></SectionTital>
            <div
              class="recipe d-flex flex-wrap align-items-center justify-content-center p-3"
              v-for="obj in similar.recipes.slice(0, 4)"
              :key="obj.id"
            >
              <div class="recipe-img">
                <img width="100%" :src="obj.image" alt="" />
              </div>

              <h4 class="ps-2 primary-color">{{ obj.title }}</h4>
            </div>
          </aside>
        </div>
      </div>
    </main>
    <FooterCom>
      <template v-slot:extra-content>
        <div class="d-flex flex-wrap mt-5 justify-content-between">
          <SectionTital
            :title="true"
            class="may-like-tital w-100 text-center font-weight-bold mb-4 mt-3"
            :subtitle="false"
            :type="'small-tital'"
            :titalText="'You may like these recipe too'"
          ></SectionTital>
          <RecipeCard
            v-for="recip in similar.recipes.slice(6)"
            :key="recip"
            :obj="recip"
            :size="'small'"
          ></RecipeCard>
        </div>
      </template>
    </FooterCom>
  </div>
</template>
<script setup>
import { onMounted, ref } from "vue";
import { useRoute } from "vue-router";
import RecipeCard from "@/components/RecipeCard.vue";
import { useHead } from "@vueuse/head"; // استيراد useHead
useHead({
  title: `Recipe instructions`,
  meta: [
    {
      name: "description",
      content:
        "Discover delicious recipes, cooking tips, and the latest food news on our website. Stay updated with the world of cuisine and elevate your cooking skills with expert advice, trending dishes, and more.",
    },
    {
      name: "keywords",
      content:
        "recipes, cooking tips, food news, cuisine, food trends, cooking recipes, healthy recipes, delicious meals, kitchen tips, food blogs, international cuisine, cooking techniques, culinary news, easy recipes",
    },
  ],
});
let recipe = ref(null);
const route = useRoute();
const similar = ref(null);
const http = async () => {
  try {
    let response = await fetch(
      `https://api.spoonacular.com/recipes/${route.params.id}/information?includeNutrition=true&apiKey=cd216c403a9948fbaab18df612d15354`
    );
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    recipe.value = await response.json();
    console.log(recipe.value);
  } catch (error) {
    console.error(`Error fetching random recipes: ${error.message}`);
  }
};
const otherRecipe = async () => {
  try {
    let response = await fetch(
      `https://api.spoonacular.com/recipes/random?apiKey=cd216c403a9948fbaab18df612d15354&number=10
`
    );
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    similar.value = await response.json();
    console.log(similar.value);
  } catch (error) {
    console.error(`Error fetching random recipes: ${error.message}`);
  }
};
http();
otherRecipe();
const printPage = () => {
  window.print();
};

onMounted(() => {});
//1e4f3628160f4bfb8d6e380d56aa703c
</script>
